.quality {
    margin-bottom: 49px;
}

.accordion {
    margin: 10px 0;
    color: $white;
    width: 70%;
    margin: auto;

    @media screen and (max-width: 450px) {
        width: 90%;
    }

    .accordion-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        padding: 12px;
        margin-top: 20px;
        margin-bottom: 20px;
        cursor: pointer;
        background-color: $color-1;
        border: none;
        border-radius: 10px;
        transition: transform 0.5s ease-in-out, background-color 0.5s ease-in-out;
        z-index: 1;

        @media screen and (max-width: 450px) {
            margin-bottom: 0;
        }

        h2 {
            margin: 0;
        }

        .fa-chevron-down {
            transition: transform 0.5s ease-in-out;
        }

        .fa-rotate-180 {
            transform: rotate(180deg);
        }
    }


    $tempsAnimation: 0.1s;

    .content {
        max-height: 0;
        overflow: hidden;
        padding: 0 10px;
        border-radius: 5px;
        background-color: $color-3;
        color: $color-1;
        opacity: 0;
        transform: translateY(-50%);
        transition: max-height $tempsAnimation ease-in-out, opacity $tempsAnimation ease-in-out, transform $tempsAnimation ease-in-out, visibility 0s $tempsAnimation;
        transform-origin: top;

        &.opening {
            display: flex;
            align-items: center;
            padding: 15px;
            max-height: fit-content;
            opacity: 1;
            margin-top: -30px;
            margin-bottom: 10px;
            border-radius: 10px;
            transform: translateY(0%);
            transition: opacity 0.3s ease, margin 0.3s ease, transform 0.3s ease;

        }

        &.closing {
            max-height: 0;
            opacity: 0;
            transform: translateY(-100%);


        }
    }
}